// Code réutilisable

@mixin large-border {
    border-radius: $large-border-radius;
  }
  
  @mixin medium-border {
    border-radius: $medium-border-radius;
  }
  
  @mixin small-border {
    border-radius: $small-border-radius;
  }
  
  @mixin flex-display(
    $di: row,
    $jc: initial,
    $ai: initial,
    $wr: nowrap,
    $ga: normal
  ) {
    display: flex;
    flex-direction: $di;
    justify-content: $jc;
    align-items: $ai;
    flex-wrap: $wr;
    gap: $ga;
  }
  
  @mixin dimensions($h: 100%, $w: 100%) {
    height: $h;
    width: $w;
  }
  
  @mixin font($fs: 18px, $fw: 400, $lh: normal, $ta: left) {
    font-size: $fs;
    font-weight: $fw;
    @if ($lh != normal) {
      line-height: $lh;
    }
    @if ($ta != left) {
      text-align: $ta;
    }
  }
  
  @mixin desktop-breakpoint {
    @media screen and (min-width: $desktop-breakpoint) {
      @content;
    }
  }