@import '../utils/mixins';
@import '../utils/variables';


.slideshow {
    max-width: $max-width;
    margin: $nomargin-center;

  }
  
  .image-container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  
  .arrow {
    max-width: 50px;
    height: 50px;
    cursor: pointer;
    /* position: absolute; */
    top: 50%;
    /* transform: translateY(-50%); */
    z-index: 1; /* Pour s'assurer que les flèches restent au-dessus des images */
  }
  
  .left-arrow {
    left: 10px;
  }
  
  .right-arrow {
    right: 10px;
  }
  
  // .gallery-image {
  //   width: 100%;
  //   max-height: 400px;
  //   margin: $nomargin-center;
  // }
  
  img.arrow.left-arrow,
  img.arrow.right-arrow{
    @media screen and (max-width: 550px){
      height: 24px;
      width: 24px;
    }
  }